  position: relative;
  z-index: 1;
  margin-bottom: 50px;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}

span.line_title {
  position: absolute;
  z-index: 1;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  width: 168px;
  text-align: center;
  border-bottom: 1px solid #333333;
  padding: 9px 0;
  top: 76px;
  left: 100%;
  margin-left: 25px;
  transition: .5s ease all;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
}

.slick-active span.line_title {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.color_box {
  background: #F9EFE1;
  padding: 14px 20px;
  margin-bottom: 60px;
  position: relative;
  z-index: 1;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}

.unique_right_content .blackBtn {
  position: relative;
  z-index: 1;
  -webkit-transform: translateY(-50px);
          transform: translateY(-50px);
  opacity: 0;
}

.slick-active h2.unique_line_title {
  transition: .5s ease all;
  transition-delay: .7s;
}

.slick-active .color_box {
  transition: .5s ease all;
  transition-delay: 1s;
}

.slick-active .unique_right_content .blackBtn {
  transition: .5s ease all;
  transition-delay: 1.3s;
}

.slick-active h2.unique_line_title,
.slick-active .color_box,
.slick-active .unique_right_content .blackBtn {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  opacity: 1;
}

.slick-list {
  overflow: visible !important;
}

